<template>
  <div>
    <v-radio-group v-model="tagType" row>
      <v-radio label="By Type" value="byType"></v-radio>
      <v-radio label="By Selection" value="bySelection"></v-radio>
      <v-radio label="Custom" value="custom"></v-radio>
    </v-radio-group>
    <div v-if="tagType === 'custom'">
      <v-row :key="index" v-for="(tag, index) of tags">
        <v-col cols="5">
          <v-text-field @input="v => onChange(tags)" v-model="tag.key" label="Key"></v-text-field>
        </v-col>
        <v-col cols="5">
          <v-text-field @input="v => onChange(tags)" v-model="tag.value" label="Value"></v-text-field>
        </v-col>
        <v-col class="wrap-remove" cols="2">
          <v-btn icon @click="clickRemove(index)">
            <v-icon>mdi-close-circle</v-icon>
          </v-btn>
        </v-col>
      </v-row>
      <v-row>
        <v-col cols="12">
          <v-btn block small @click="clickAdd"> Add </v-btn>
        </v-col>
      </v-row>
    </div>
    <div v-if="tagType === 'byType'">
      <v-combobox @change="onChangeTypeName" :items="typeNames" label="Types"></v-combobox>
    </div>
    <div v-if="tagType === 'bySelection'">
      <v-combobox
        item-text="name"
        @change="onChangeSelectionTag"
        :items="selectionTags"
        label="Select tags"
      ></v-combobox>
    </div>
  </div>
</template>

<script>
import { getTypeNames, getTypeTags } from '../../utils/tagsUtils';
export default {
  props: ['items', 'onChange'],
  data() {
    return {
      tagType: 'custom',
      typeNames: getTypeNames(),
      tags: [...this.items],
      selectionTags: [],
    };
  },
  watch: {
    items(val) {
      this.tags = [...val];
      this.tagType= 'custom';
    },
  },
  mounted() {
    this.getSelectionTags();
  },
  methods: {
    onChangeSelectionTag({ data }) {
      this.onChange(data);
    },
    getSelectionTags() {
      const { tags } = this.$store.state.setting;
      this.selectionTags = tags;
    },
    onChangeTypeName(val) {
      const typeTags = getTypeTags(val);
      const items = [];
      for (const tag of typeTags) {
        items.push({ key: tag, value: tag });
      }
      this.onChange(items);
    },
    clickAdd() {
      this.tags.push({});
    },
    clickRemove(index) {
      this.tags.splice(index, 1);
      this.onChange(this.tags);
    },
  },
};
</script>
<style>
.v-card__actions {
  display: flex;
  align-items: center;
  justify-content: center;
}
.wrap-remove {
  display: flex;
  align-items: center;
  justify-content: center;
}
.v-autocomplete__content.v-menu__content {
  text-align: left;
}
</style>
